.hero1{
    display: flex;
}
.hero{
    background-color: #fff;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: space-evenly;
    padding: 120px;
}

.hero:before {
    content: '';
    background: url(/criberr-deploy/static/media/headerbg.aaba6306.png) no-repeat cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}
.heroh1{
    color: var(--primary-dark);
    font-size: 3rem;
    font-weight: 800;
    
}
.heroh{
    color: var(--primary-dark);
    font-size: 3rem;
    font-weight: 800;
}


.herop{
font-size: 1.2rem;
font-weight: 700;
margin-left: 10px;
}
.hero-img{
    width: 400px;
    margin: auto;
    margin-left: 40px;
    margin-top: 40px;
}
.hero .button{
    margin: 1rem;
}
.hero .button:hover{
    background-color: #fff;
    cursor: pointer;
}

@media screen and (max-width: 940px) {
   .hero1{
    padding: 0;
    margin: 0;
   }
    .hero{
    max-height: 100%;
    padding-top: 50px;
   }
    .heroh1, .heroh{
        font-size: 2rem;
    }
    .herop{
        font-size: 0.8rem;
    }
    .hero-img{
        width: 300px;
        max-height: 100%;
        align-items: center;
        float: center;

    }

}
    
#button {
    padding: 2px 10px;
    font-size: 1.2rem;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    background-color: #0646a5;
    background: linear-gradient(to right, var(--primary-color), var(--primary-dark)); 
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
    border: none;
    margin: 20px 10px;
    outline: none;
    border-radius: 7px;
    justify-content: center;
    align-items: center;
    cursor:pointer;
  }
  @media screen and (max-width:940px) {
    #button{
      font-size: 0.9rem;
    }
  }
.form-desgin{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.para-sucess{
    background-color: greenyellow;
    padding: 10px;
    text-align: center;
    justify-content: center;
    color: #0A4AAE;
    font-weight: 600;
    font-size: 1rem;
    max-width: 100%;
    max-height: 100%;
}
.share-icon{
   margin-top: 10px;
    grid-gap: 1rem;
    gap: 1rem;
    max-width: 100%;
    max-height: 100%;
}
@media screen and (max-width: 940px){
.buttonItemLayout{
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 100%;
    max-width: 100%;
}
.para-sucess, .share-icon{
    max-width: 100%;
    max-height: 100%;
}
}
.about-us{
    max-height: 100%;
    grid-gap: 2rem !important;
    gap: 2rem !important;
    background-size: cover;
    align-content: center;
    justify-content: space-between;
    display: flex;
    flex-direction: center;
}

.who{
    margin-top: 100px;
    margin-left: 150px ;
    margin-bottom: 25px;
    align-items: flex-end;
    float: right;
    padding: auto;
}
.who-we-are{
    color: #094AAD;
    font-size: 1.125;
}
.who-we-are-2{
    font-size: 2.25
}

p{
    font-size: 1.125;
}

.about-img{
    width: 400px
}
summary{
    color: #094AAD;
}
@media screen and (max-width:940px) {
    .about-us{
        max-height: 100%;
        
    }
    .about{
        display: block;
    }
    .who{
        margin: auto;
        text-align: center;
    }
    .who-we-are{
        font-size: 1.5rem;
    }
    .who-we-are-2{
        font-size: 1rem;
    }
    p{
        font-size: 0.8rem; 
    }
    
}

.features{
    background: #F2F8FF;
    height: 90vh;
    background-size: cover;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.get-started{
    text-align: start;
    margin-top: 100px;
}
.started{
    color: #094AAD;
}

.get-started li {
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    list-style: none;
    line-height: 2;
}

.feature-img {
    width: 200px;
    margin-left: 20px;
}

.our-service{
    margin-top: 50px;
}
@media screen and (max-width: 940px) {
    .features{
        padding: 25px;
        width: 100%;
        text-align: center;
        max-height: 100%;
        display: block;
        align-items: center;
    }   
    .get-started{
        align-items: center;
        margin-right: 50px;
        margin-top: 0;
    }
    .feature-img{
        width: 100px;
        margin:auto;
        align-items: center;
        margin-top: -50px;
    }
    .started h1{
        text-align: center;
    }
    .get-started li {
        float: none;
        width: auto;
        list-style: none;
        height: 1.2;
        font-size: 0.8rem;
    }
    li:last-child{
        margin-bottom: 70px;
    }
    .our-service a{
        font-size: 1rem;
        margin-top: 0;
    }
}
#list-form{
    background: url(/criberr-deploy/static/media/headerbg.aaba6306.png);
    background-color: #094AAD;
}
.form{
    padding: 50px;
    border-radius: 10px;
}
.footer {
    height: 5vh;
    display: flex;
    justify-content: space-between;
    padding: auto;
    margin: 10px 40px;
}
.foot-text{
    color: #094AAD;
    font-size: 1.2rem;
    font-weight: 700;
}
.foot-app ul{
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
    color: #094AAD;
    font-size: 1.5rem;
}
.copyright{
    font-weight: 400;
    color: #094AAD;
}
.copyright sup{
    color: #094AAD;
    font-size: .8rem;
}

@media screen and (max-width: 940px){

    .footer{
        max-width: 100%;
        display: flex;
        vertical-align: middle;
        float: none;
    }
    .foot-text{
        font-size: 0.7rem;
    }
    .foot-app ul{
        font-size: 1rem;
        grid-gap: none;
        gap: none;
    }
    .copyright, .copyright sup{
        display: none;
    }
}
.header {
    background-color: #fff;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
    padding: 20px;
}

.header .nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 3rem;
}
.header .nav img{
    width: 200px;
    height: auto;
}

.header .nav-menu {
    display: flex;
}
.header .nav-item{
    padding: 1rem;
    font-weight: 700;
}

.header .nav-item a:hover {
    padding-bottom: 10px;
    border-bottom: 3px solid var(--primary-dark);
}

.hamburger {
    display: none;
}
.button{
    color: #fff;
    background-color: #0A4AAE;
    padding: 5px;
    border-radius: 5px;
    border: 2px solid var(--primary-color);
}

@media screen and (max-width: 520px){
    .header{
        max-width: 100%;
        max-height: 100%;
        
    }
    .header .nav{
        max-width: 100%;
        max-height: 100%;
    }

    .hamburger{
        display: block;
    }

    .nav-menu {
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: #fff;
        width: 100%;
        height: 60vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }
    .nav-item{
        margin: 1rem;
    }
   
    .header .nav img{
        max-width: 50%;
    }
}
:root{
  --primary-color:#094aad;
  --primary-dark:#0646a5;
  --overlay: rgb(0,0,0,.7)

}
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.1rem;
  line-height: 1.2;
}

code {
  font-family: 'Montserrat', sans-serif;
}

ul{
  list-style-type: none;
}
a{
  text-decoration: none;
  color: #094aad;
  color: var(--primary-color);
}







